html.dark,
html:not(.dark) .codeblock-dark {
  /*********************************************************
  * Tokens
  */
  .namespace {
    opacity: 0.7;
  }

  .token.doctype .token.doctype-tag {
    color: #569cd6;
  }

  .token.doctype .token.name {
    color: #9cdcfe;
  }

  .token.comment,
  .token.prolog {
    color: #6a9955;
  }

  .token.punctuation,
  .language-html .language-css .token.punctuation,
  .language-html .language-javascript .token.punctuation {
    color: #d4d4d4;
  }

  .token.property,
  .token.tag,
  .token.boolean,
  .token.number,
  .token.constant,
  .token.symbol,
  .token.inserted,
  .token.unit {
    color: #b5cea8;
  }

  .token.selector,
  .token.attr-name,
  .token.string,
  .token.char,
  .token.builtin,
  .token.deleted {
    color: #ce9178;
  }

  .token.plain-text {
    color: #fafafa;
  }

  .language-css .token.string.url {
    text-decoration: underline;
  }

  .token.operator,
  .token.entity {
    color: #d4d4d4;
  }

  .token.operator.arrow {
    color: #569cd6;
  }

  .token.atrule {
    color: #ce9178;
  }

  .token.atrule .token.rule {
    color: #c586c0;
  }

  .token.atrule .token.url {
    color: #9cdcfe;
  }

  .token.atrule .token.url .token.function {
    color: #dcdcaa;
  }

  .token.atrule .token.url .token.punctuation {
    color: #d4d4d4;
  }

  .token.keyword {
    color: #569cd6;
  }

  .token.keyword.module,
  .token.keyword.control-flow {
    color: #c586c0;
  }

  .token.function,
  .token.function .token.maybe-class-name {
    color: #dcdcaa;
  }

  .token.regex {
    color: #d16969;
  }

  .token.important {
    color: #569cd6;
  }

  .token.italic {
    font-style: italic;
  }

  .token.constant {
    color: #9cdcfe;
  }

  .token.class-name,
  .token.maybe-class-name {
    color: #4ec9b0;
  }

  .token.console {
    color: #9cdcfe;
  }

  .token.parameter {
    color: #9cdcfe;
  }

  .token.interpolation {
    color: #9cdcfe;
  }

  .token.punctuation.interpolation-punctuation {
    color: #569cd6;
  }

  .token.boolean {
    color: #569cd6;
  }

  .token.property,
  .token.variable,
  .token.imports .token.maybe-class-name,
  .token.exports .token.maybe-class-name {
    color: #9cdcfe;
  }

  .token.selector {
    color: #d7ba7d;
  }

  .token.escape {
    color: #d7ba7d;
  }

  .token.tag {
    color: #569cd6;
  }

  .token.tag .token.punctuation {
    color: #808080;
  }

  .token.cdata {
    color: #808080;
  }

  .token.attr-name {
    color: #9cdcfe;
  }

  .token.attr-value,
  .token.attr-value .token.punctuation {
    color: #ce9178;
  }

  .token.attr-value .token.punctuation.attr-equals {
    color: #d4d4d4;
  }

  .token.entity {
    color: #569cd6;
  }

  .token.namespace {
    color: #4ec9b0;
  }

  .token.operator {
    @apply text-slate-400;
  }

  /*********************************************************
  * Language Specific
  */

  pre[class*='language-javascript'],
  code[class*='language-javascript'],
  pre[class*='language-jsx'],
  code[class*='language-jsx'],
  pre[class*='language-typescript'],
  code[class*='language-typescript'],
  pre[class*='language-tsx'],
  code[class*='language-tsx'] {
    color: #9cdcfe;
  }

  pre[class*='language-css'],
  code[class*='language-css'] {
    color: #ce9178;
  }

  pre[class*='language-html'],
  code[class*='language-html'] {
    color: #d4d4d4;
  }

  .language-regex .token.anchor {
    color: #dcdcaa;
  }

  .language-html .token.punctuation {
    color: #808080;
  }

  code.language-toml .table {
    display: inherit;
  }

  /*********************************************************
  * Line highlighting
  */
  pre[class*='language-'] > code[class*='language-'] {
    position: relative;
    z-index: 1;
    display: block;
    width: max-content;
    min-width: 100%;
  }

  .line-highlight {
    background: rgb(var(--primary-light) / 0.2);
    width: 100%;
    display: inline-block;
    position: relative;
    z-index: 0;
  }

  .line-highlight::before,
  .line-highlight::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1.25rem;
    background: rgb(var(--primary-light) / 0.2);
  }

  .line-highlight::before {
    left: -1.25rem;
    border-left: 4px solid rgb(var(--primary-light) / 1);
  }
  .line-highlight::after {
    right: -1.25rem;
  }

  pre[class^='language-diff-'] {
    @apply flex px-9;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }
}

html:not(.dark) .codeblock-light {
  /*********************************************************
  * Tokens
  */
  .namespace {
    opacity: 0.7;
  }
  .token.comment,
  .token.prolog,
  .token.doctype,
  .token.cdata {
    color: #6e7781;
  }
  .token.punctuation {
    color: #24292f;
  }
  .token.property,
  .token.tag,
  .token.boolean,
  .token.number,
  .token.constant,
  .token.symbol,
  .token.deleted {
    color: #0550ae;
  }
  .token.selector,
  .token.attr-name,
  .token.string,
  .token.char,
  .token.builtin,
  .token.inserted {
    color: #0a3069;
  }
  .token.operator,
  .token.entity,
  .token.url,
  .language-css .token.string,
  .style .token.string {
    color: #0550ae;
  }
  .token.atrule,
  .token.attr-value,
  .token.keyword {
    color: #cf222e;
  }
  .token.function {
    color: #8250df;
  }
  .token.regex,
  .token.important,
  .token.variable {
    color: #0a3069;
  }
  .token.important,
  .token.bold {
    font-weight: bold;
  }
  .token.italic {
    font-style: italic;
  }
  .token.entity {
    cursor: help;
  }

  /*********************************************************
  * Language Specific
  */

  .language-css .token.selector {
    color: #116329;
  }

  .language-css .token.property {
    color: #0550ae;
  }

  .language-css .token.important,
  .language-css .token.atrule .token.rule {
    color: #cf222e;
  }

  .language-js .token.keyword,
  .language-javascript .token.keyword {
    color: #cf222e;
  }

  .language-js .token.operator,
  .language-js .token.constant,
  .language-js .token.boolean,
  .language-js .token.number,
  .language-js .token.atrule,
  .language-javascript .token.operator,
  .language-javascript .token.constant,
  .language-javascript .token.boolean,
  .language-javascript .token.number,
  .language-javascript .token.atrule {
    color: #0550ae;
  }

  .language-js .token.function,
  .language-javascript .token.function {
    color: #8250df;
  }

  .language-js .token.attr-name,
  .language-js .token.class-name,
  .language-js .token.function-variable,
  .language-javascript .token.attr-name,
  .language-javascript .token.class-name,
  .language-javascript .token.function-variable {
    color: var(--color-brown);
  }

  .language-jsx .token.keyword {
    color: #cf222e;
  }

  .language-jsx .token.function {
    color: #8250df;
  }

  .language-jsx .token.function-variable {
    color: var(--color-brown);
  }

  .language-jsx .token.punctuation {
    color: #0550ae;
  }

  .language-jsx .token.attr-name,
  .language-jsx .token.class-name {
    color: #116329;
  }

  .language-jsx .token.string {
    color: #0a3069;
  }

  .language-json .token.operator {
    color: #24292f;
  }

  .language-json .token.null.keyword {
    color: #0550ae;
  }

  code.language-toml .table {
    display: inherit;
  }

  /*********************************************************
  * Line highlighting
  */
  pre[class*='language-'] > code[class*='language-'] {
    position: relative;
    z-index: 1;
    display: block;
    width: max-content;
    min-width: 100%;
  }

  .line-highlight {
    background: rgb(var(--primary-light) / 0.2);
    width: 100%;
    display: inline-block;
    position: relative;
    z-index: 0;
  }

  .line-highlight::before,
  .line-highlight::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1.25rem;
    background: rgb(var(--primary-light) / 0.2);
  }

  .line-highlight::before {
    left: -1.25rem;
    border-left: 4px solid rgb(var(--primary-light) / 1);
  }
  .line-highlight::after {
    right: -1.25rem;
  }

  pre[class^='language-diff-'] {
    @apply flex px-9;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }

  pre[class^='language-diff-'] > code {
    @apply flex-none min-w-full;
  }
}
